export default({name, data = {}}) => {
	return new Promise((resolve, reject) => {
		// loading
		uni.showLoading({})
		// 
		uniCloud.callFunction({
			// 
			name,
			data,
			// 
			success: ({result}) => {
				// 200
				if (result.code === 200) {
					resolve(result)
				} else {
					// 
					uni.showToast({
						icon: 'none',
						title: result.msg
					})
				}
			},
			// 
			fail: (err) => {
				reject(err)
			},
			// loading
			complete() {
				uni.hideLoading()
			}
		})
	})
}
